import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import './BookDetail.css';
import { toast } from 'react-toastify';
import { useNavigate } from "react-router-dom";
import { formatVND } from '../../common';

const BookDetail = () => {
    const { bookId } = useParams();
    const [bookDetail, setBookDetail] = useState(null);
    const [quantity, setQuantity] = useState(1);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const { isAdded } = true;
    const navigate = useNavigate();

    const incrementQuantity = () => {
        if (quantity < bookDetail.stockQuantity)
            setQuantity(prevQuantity => prevQuantity + 1);
    };

    const decrementQuantity = () => {
        setQuantity(prevQuantity => Math.max(prevQuantity - 1, 1));
    };

    useEffect(() => {
        axios.get(`http://localhost:8080/api/book/detail/${bookId}`)
            .then(response => {
                setBookDetail(response.data);
                setLoading(false);
            })
            .catch(error => {
                console.error('Error fetching book details:', error);
                setError(error);
                setLoading(false);
            });
    }, [bookId]);

    if (loading) {
        return <div>Loading...</div>;
    }

    if (error) {
        return <div> c li xy ra khi ti thng tin sch.</div>;
    }

    const authors = bookDetail.authors.map(a => a.authorName).join(', ');
    const publicationYear = new Date(bookDetail.publication_date).getFullYear();
    const genres = bookDetail.genres.map(g => g.genreName).join(', ');

    const addToCart = async () => {
        const accessToken = localStorage.getItem('accessToken');
        try {
            const response = await axios.put('http://localhost:8080/api/user/orm/carts/book', {
                bookId,
                quantity,
                isAdded: true
            }, {
                headers: {
                    'Authorization':`Bearer ${accessToken}`
                }
            });
            toast.success("Thm vo gi hng thnh cng", {
                position: toast.POSITION.TOP_RIGHT,
                autoClose: 2000,
            });
            navigate("/shop");
        } catch (error) {
            console.error('Error updating cart:', error);
            toast.error("Vui lng ng nhp  mua sn phm !", {
                position: toast.POSITION.TOP_RIGHT,
                autoClose: 2000,
            });
            navigate("/login")
        }
    };

    return (
        <div className="book-detail-container">
            <div className="book-detail-image">
                <img  src={bookDetail.imgUrl} alt={bookDetail.title} />
                <div className="book-detail-info">
                    <h1>{bookDetail.title}</h1>
                    <p className="price">{formatVND(bookDetail.price)}</p>
                    <p className="author">Tc gi: {authors}</p>
                    <p className="publication-year">Nm xut bn: {publicationYear}</p>
                    <p className="genre">Th loi: {genres}</p>
                    <p className="stock-quantity">
                        S lng sn c: {bookDetail.stockQuantity > 0 ? `${bookDetail.stockQuantity} sch` : <span style={{ color: 'red' }}>Ht hng</span>}
                    </p>

                    {bookDetail.stockQuantity > 0 ? (
                        <>
                            <div className="quantity-controls">
                                <button
                                    onClick={decrementQuantity}
                                    disabled={quantity === 1}
                                    aria-label="Gim s lng"
                                >-</button>
                                <span>{quantity}</span>
                                <button
                                    onClick={incrementQuantity}
                                    disabled={bookDetail.stockQuantity === 0}
                                    aria-label="Tng s lng"
                                >+</button>
                            </div>
                            <div className="add-to-cart">
                                <button onClick={addToCart}>Thm vo gi hng</button>
                            </div>
                        </>
                    ) : (
                        <p style={{ color: 'red', fontWeight: 'bold' }}></p>
                    )}

                </div>
            </div>
            <div className="description">
                <h1 style={{color: "#228b22"}}>Gii thiu v sch</h1>
                <p style={{textAlign: "justify"}}>{bookDetail.description}</p>
            </div>
        </div>
    );
};

export default BookDetail;
